import Request from '@/utils/request'

/**
 * 星流start文生图请求
 * @param {Object} data 参数对象
 * @returns {Promise} 请求Promise
 */
export function getStartTextToImage(data) {
  // 构建正确的请求参数
  const requestData = {
    prompt: data.prompt,
    aspectRatio: 'square', 
    imgCount: data.imgCount,
    imageSize: {
      width: data.width,
      height: data.height
    },
    userId: 1
  }
  
  return Request.post('/api/image/start/text-to-image', requestData)
} 