<template>
  <div class="image-to-image-tab">
    <div class="tab-content">
      <!-- 左侧控制面板 -->
      <div class="control-panel">
        <!-- 图片上传区域 -->
        <div class="upload-section">
          <image-uploader v-model="sourceImage" :main-text="'拖放图片或点击上传'" :sub-text="'支持JPG/PNG格式，最大5MB'"
            @change="handleImageUpload" />
        </div>

        <!-- 提示词区域 -->
        <div class="prompts-section">
          <!-- 正向提示词 -->
          <div class="prompt-input">
            <label>提示词 Prompt</label>
            <el-input v-model="prompts.positive" type="textarea" :rows="5" placeholder="请输入正向提示词描述你想要的图像内容"
              resize="none" maxlength="1000" show-word-limit />
            <div class="prompt-tips">
              提示：使用详细和具体的描述，提及风格、光线、视角和场景会得到更好的结果
              <el-button type="text" size="mini" @click="translatePrompt">翻译为英文</el-button>
              <el-button type="text" size="mini" @click="showPromptLibrary">提示词库</el-button>
            </div>
          </div>

          <!-- 负向提示词 -->
          <div class="negative-prompt">
            <label>负向提示词 Negative Prompt</label>
            <el-input v-model="prompts.negative" type="textarea" :rows="3" placeholder="请输入负向提示词描述你不想在图像中出现的元素"
              resize="none" maxlength="500" show-word-limit />
            <div class="prompt-tips">
              <el-button type="text" size="mini" @click="useDefaultNegativePrompt">使用默认</el-button>
              <el-button type="text" size="mini" @click="showNegativeLibrary">负向提示词库</el-button>
            </div>
          </div>
        </div>

        <!-- 模型和设置 -->
        <div class="settings-section">
          <div class="model-selection">
            <!-- 模型选择器 -->
            <div class="model-header-row">
              <div class="checkpoint-selector">
                <span class="model-label">基础模型</span>
                <div class="selector-container">
                  <el-select v-model="settings.model" placeholder="选择基础模型" class="model-select">
                    <el-option v-for="item in modelList" :key="item.value" :label="item.label" :value="item.value">
                      <span>{{ item.label }}</span>
                      <span class="model-description">{{ item.description }}</span>
                    </el-option>
                  </el-select>
                  <el-button type="primary" icon="el-icon-refresh" size="mini" circle class="refresh-btn"
                    @click="refreshModels"></el-button>
                </div>
              </div>
            </div>

            <!-- 模板选择器 -->
            <div class="model-header-row">
              <div class="checkpoint-selector">
                <span class="model-label">模板</span>
                <div class="selector-container">
                  <el-select v-model="settings.template" placeholder="选择模板" class="model-select">
                    <el-option v-for="item in templateList" :key="item.value" :label="item.label" :value="item.value">
                      <span>{{ item.label }}</span>
                      <span class="model-description">{{ item.description }}</span>
                    </el-option>
                  </el-select>
                </div>
              </div>
            </div>

            <!-- CLIP跳过 -->
            <div class="clip-skip">
              <div class="clip-header">
                <span class="model-label">CLIP跳过层Clip Skip</span>
                <el-tooltip content="控制CLIP模型内部使用第几层作为文本特征" placement="top">
                  <i class="el-icon-question"></i>
                </el-tooltip>
              </div>
              <div class="clip-controls">
                <el-input-number v-model="clipSkipValue" :min="1" :max="12" :step="1" size="small"
                  class="clip-input"></el-input-number>
                <el-switch v-model="settings.useClipSkip" class="clip-switch"></el-switch>
              </div>
            </div>
          </div>

          <!-- 图像设置 -->
          <div class="image-settings">
            <div class="section-header">
              <h3>图像设置</h3>
            </div>
            <div class="section-content">
              <!-- 采样器设置 -->
              <div class="setting-row">
                <div class="setting-label">采样器</div>
                <div class="setting-control">
                  <el-select v-model="settings.sampler" placeholder="选择采样器" size="small" @change="updateTokenUsage">
                    <el-option v-for="item in samplerList" :key="item.value" :label="item.label" :value="item.value" />
                  </el-select>
                </div>
              </div>

              <!-- 新增的图像尺寸设置 -->
              <div class="setting-row">
                <div class="setting-label">图像尺寸</div>
                <div class="setting-control size-inputs">
                  <el-input-number v-model="settings.width" :min="256" :max="2048" :step="64" size="small"
                    @change="validateSize" />
                  <span class="size-separator">×</span>
                  <el-input-number v-model="settings.height" :min="256" :max="2048" :step="64" size="small"
                    @change="validateSize" />
                </div>
              </div>

              

              <!-- 步数设置 -->
              <div class="setting-row">
                <div class="setting-label">步数</div>
                <div class="setting-control">
                  <el-slider v-model="settings.steps" :min="1" :max="150" :step="1" show-input size="small"
                    @change="updateTokenUsage" />
                </div>
              </div>

              <!-- CFG Scale设置 -->
              <div class="setting-row">
                <div class="setting-label">CFG Scale</div>
                <div class="setting-control">
                  <el-slider v-model="settings.cfgScale" :min="1" :max="30" :step="0.5" show-input size="small"
                    @change="updateTokenUsage" />
                </div>
              </div>

              <!-- 去噪强度设置 -->
              <div class="setting-row">
                <div class="setting-label">去噪强度</div>
                <div class="setting-control">
                  <el-slider v-model="settings.denoisingStrength" :min="0" :max="1" :step="0.05" show-input
                    size="small" />
                </div>
              </div>

              <!-- 批次大小设置 -->
              <div class="setting-row">
                <div class="setting-label">批次大小</div>
                <div class="setting-control">
                  <el-input-number v-model="settings.imgCount" :min="1" :max="isVipUser ? 4 : 1" :step="1" size="small"
                    @change="updateTokenUsage" />
                  <el-tooltip v-if="!isVipUser && settings.imgCount > 1" content="批量生成需要VIP会员" placement="top">
                    <i class="el-icon-warning-outline" style="color: #E6A23C;"></i>
                  </el-tooltip>
                </div>
              </div>

              <!-- 种子设置 -->
              <div class="setting-row">
                <div class="setting-label">随机种子</div>
                <div class="setting-control seed-row">
                  <el-input-number v-model="settings.seed" :min="-1" :max="2147483647" :step="1" size="small" />
                  <el-tooltip content="使用-1表示随机种子" placement="top">
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  <el-button size="mini" icon="el-icon-refresh" @click="randomizeSeed"
                    class="seed-random-btn"></el-button>
                </div>
              </div>
            </div>
          </div>

          <!-- 高清修复设置 -->
          <div class="advanced-settings-toggle">
            <div class="section-header">
              <h3>高清修复设置</h3>
              <el-switch v-model="settings.useHiresFix" @change="updateTokenUsage" />
            </div>
            <div v-if="settings.useHiresFix" class="hires-fix-settings">
              <div class="setting-row">
                <div class="setting-label">高清修复步数</div>
                <div class="setting-control">
                  <el-input-number v-model="settings.hiresSteps" :min="1" :max="50" :step="1" size="small" />
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">去噪强度</div>
                <div class="setting-control">
                  <el-slider v-model="settings.hiresDenoisingStrength" :min="0" :max="1" :step="0.05" />
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">放大倍数</div>
                <div class="setting-control">
                  <el-select v-model="settings.upscaler" placeholder="选择放大倍数">
                    <el-option label="1.5x" :value="1.5"></el-option>
                    <el-option label="2x" :value="2"></el-option>
                  </el-select>
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">缩放宽度</div>
                <div class="setting-control">
                  <el-input-number v-model="settings.resizedWidth" :min="settings.width" :max="2048" :step="64"
                    size="small" />
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">缩放高度</div>
                <div class="setting-control">
                  <el-input-number v-model="settings.resizedHeight" :min="settings.height" :max="2048" :step="64"
                    size="small" />
                </div>
              </div>
            </div>
          </div>

          <!-- 高级设置 -->
          <div class="advanced-settings-toggle">
            <div class="section-header">
              <h3>高级设置</h3>
              <el-switch v-model="showAdvancedSettings" />
            </div>
            <div v-if="showAdvancedSettings" class="advanced-settings">
              <div class="section-content">
                <div class="setting-row">
                  <div class="setting-label">面部修复</div>
                  <div class="setting-control">
                    <el-switch v-model="settings.faceRestoration" @change="updateTokenUsage"></el-switch>
                    <span class="restore-face-hint" v-if="settings.faceRestoration">将应用面部修复</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- 生成按钮区域 -->
        <div class="generate-button-section">
          <div class="token-info">
            <div class="token-count">
              <span>预计消耗积分: <b>{{ totalTokens }}</b></span>
              <el-tooltip content="积分消耗根据图像分辨率、步数和批次计算" placement="top">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </div>
            <div class="user-tokens">
              <span>可用积分: <b>{{ userTokens }}</b></span>
              <el-button type="text" size="mini" @click="openBuyTokens">购买积分</el-button>
            </div>
          </div>

          <generate-button :token-cost="totalTokens" :token-balance="userTokens" :is-generating="isGenerating"
            :generation-progress="generationProgress" :can-generate="canGenerate"
            :progress-text="progressText || '生成中...'" :show-stop-button="true" :is-vip-user="isVipUser"
            @generate="handleGenerate" @stop="handleStop" @recharge="openBuyTokens" @open-vip="openVipOptions" />

          <div class="generation-limits" v-if="!isVipUser">
            <p>免费用户每天限制生成 {{ freeUserDailyLimit }} 张图片 <el-button type="text"
                @click="openVipOptions">升级会员解锁更多</el-button>
            </p>
          </div>
        </div>
      </div>

      <!-- 右侧图片展示区域 -->
      <div class="image-preview-panel">
        <div class="preview-header">
          <h3 class="preview-title">生成预览</h3>
          <div class="preview-actions" v-if="generatedImages.length > 0">
            <el-button size="small" type="primary" icon="el-icon-download" @click="saveAllImages">保存所有</el-button>
            <el-button size="small" icon="el-icon-delete" @click="clearResults">清除结果</el-button>
          </div>
        </div>

        <div class="preview-content">
          <div v-if="generatedImages.length === 0" class="empty-preview">
            <div class="empty-placeholder">
              <i class="el-icon-picture-outline"></i>
              <p>请上传图片并配置参数后点击"开始生成"按钮</p>
            </div>
          </div>

          <div v-else class="image-grid">
            <div v-for="(image, index) in generatedImages" :key="index" class="image-item">
              <div class="image-container">
                <img :src="image.url" alt="生成的图片" class="generated-image" />
              </div>
              <div class="image-actions">
                <el-button type="text" icon="el-icon-view" @click="handleImageClick(image)">查看</el-button>
                <el-button type="text" icon="el-icon-download" @click="downloadImage(image)">下载</el-button>
                <el-button type="text" icon="el-icon-star-off" @click="favoriteImage(image)">收藏</el-button>
              </div>
            </div>
          </div>
        </div>

        <div class="generation-progress" v-if="isGenerating">
          <el-progress :percentage="generationProgress" :format="progressFormat"></el-progress>
          <p>正在生成图像，请稍等...</p>
        </div>
      </div>
    </div>

    <!-- 图片预览对话框 -->
    <el-dialog title="图片预览" :visible.sync="previewDialogVisible" width="70%">
      <div v-if="currentPreviewImage" class="preview-dialog-content">
        <el-image :src="currentPreviewImage.url" :alt="'预览图片'" fit="contain">
        </el-image>

        <div class="preview-info">
          <div class="info-section">
            <h4>提示词</h4>
            <p>{{ prompts.positive }}</p>
          </div>

          <div class="info-section">
            <h4>反向提示词</h4>
            <p>{{ prompts.negative || '无' }}</p>
          </div>

          <div class="info-section">
            <h4>参数</h4>
            <div class="params-grid">
              <div class="param-item">
                <span class="label">采样器</span>
                <span>{{ settings.sampler }}</span>
              </div>
              <div class="param-item">
                <span class="label">步数</span>
                <span>{{ settings.steps }}</span>
              </div>
              <div class="param-item">
                <span class="label">CFG Scale</span>
                <span>{{ settings.cfgScale }}</span>
              </div>
              <div class="param-item">
                <span class="label">去噪强度</span>
                <span>{{ settings.denoisingStrength }}</span>
              </div>
              <div class="param-item">
                <span class="label">种子</span>
                <span>{{ currentPreviewImage.seed }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>

    <!-- 购买积分对话框 -->
    <el-dialog title="购买积分" :visible.sync="creditsDialog.visible" width="40%">
      <div class="credit-packages">
        <div v-for="(pkg, index) in creditPackages" :key="index" class="credit-package"
          :class="{ 'selected': creditsDialog.selectedPackage && creditsDialog.selectedPackage.id === pkg.id }"
          @click="selectCreditPackage(pkg)">
          <div class="package-credits">{{ pkg.amount }}积分</div>
          <div class="package-price">¥{{ pkg.price }}</div>
          <div class="package-info" v-if="pkg.bonus">
            <el-tag size="mini" type="success">赠送{{ pkg.bonus }}积分</el-tag>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="creditsDialog.visible = false">取消</el-button>
        <el-button type="primary" :disabled="!creditsDialog.selectedPackage" @click="confirmCreditPurchase">
          确认购买
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { calculateTokens, generateRandomSeed } from '@/utils/aiUtils'
import Request from '@/utils/request'
import GenerateButton from '@/components/GenerateButton.vue'
import ImageUploader from '@/components/ImageUploader.vue'
import { getModel } from "@/api/imgToImg/ImgToImg"
import { getStartImgToImg } from "@/api/imgToImg/ImgToImg"

export default {
  name: 'ImageToImageTab',
  components: {
    GenerateButton,
    ImageUploader
  },

  data() {
    return {
      generateId: '',
      taskId: '',
      sourceImage: 'https://images.unsplash.com/photo-1506744038136-46273834b3fb',
      isGenerating: false,
      generationProgress: 0,
      progressText: '生成中...',
      generatedImages: [],
      modelList: [],
      templateList: [],
      samplerList: [
        { label: 'Euler a', value: 0 },
        { label: 'Euler', value: 1 },
        { label: 'LMS', value: 2 },
        { label: 'Heun', value: 3 },
        { label: 'DPM2', value: 4 },
        { label: 'DPM2 a', value: 5 },
        { label: 'DPM++ 2S a', value: 6 },
        { label: 'DPM++ 2M', value: 7 },
        { label: 'DPM++ SDE', value: 8 },
        { label: 'DPM fast', value: 9 },
        { label: 'DPM adaptive', value: 10 },
        { label: 'LMS Karras', value: 11 },
        { label: 'DPM2 Karras', value: 12 },
        { label: 'DPM2 a Karras', value: 13 },
        { label: 'DPM++ 2S a Karras', value: 14 },
        { label: 'DPM++ 2M Karras', value: 15 },
        { label: 'DPM++ SDE Karras', value: 16 },
        { label: 'DDIM', value: 17 },
        { label: 'PLMS', value: 18 }
      ],
      upscalerList: [
        { label: 'ESRGAN_4x', value: 'esrgan_4x' },
        { label: 'R-ESRGAN 4x+', value: 'r_esrgan_4x_plus' },
        { label: 'LDSR', value: 'ldsr' }
      ],
      clipSkipValue: 1,
      currentGenerateTask: {
        generateId: null,
        taskId: null
      },

      settings: {
        imgCount: 1,
        width: 512,  // 添加默认宽度
        height: 512, // 添加默认高度
        mode: 0,
        model: '',
        template: '',
        sampler: 0,
        steps: 30,
        cfgScale: 7,
        seed: -1,
        batchSize: 1,
        denoisingStrength: 0.75,
        useClipSkip: false,
        faceRestoration: false,
        useHiresFix: false,
        hiresSteps: 20,
        hiresDenoisingStrength: 0.7,
        upscaler: 'esrgan_4x',
        resizedWidth: 768,
        resizedHeight: 768,
        painting: {
          limits: {
            minSize: 256,
            maxSize: 1024,
            minSteps: 1,
            maxSteps: 150,
            minCfgScale: 1,
            maxCfgScale: 30,
            minBatchSize: 1,
            maxBatchSize: 4,
            minUpscaleScale: 1,
            maxUpscaleScale: 4
          }
        }
      },
      prompts: {
        positive: '',
        negative: ''
      },
      showAdvancedSettings: false,
      previewDialogVisible: false,
      currentPreviewImage: null,

      // 用户信息
      isVipUser: false,
      userTokens: 50,
      freeUserDailyLimit: 3,

      // 购买积分相关
      creditsDialog: {
        visible: false,
        selectedPackage: null
      },
      creditPackages: [
        { id: 1, amount: 100, price: 10, bonus: 10 },
        { id: 2, amount: 200, price: 18, bonus: 20 },
        { id: 3, amount: 500, price: 40, bonus: 50 },
        { id: 4, amount: 1000, price: 70, bonus: 100 }
      ]
    }
  },

  computed: {
    canGenerate() {
      return !this.isGenerating &&
        this.sourceImage &&
        this.sourceImage !== '' &&
        this.prompts.positive.trim() !== '' &&
        this.settings.model !== '' &&
        this.settings.template !== '' &&
        this.userTokens >= this.totalTokens;
    },

    totalTokens() {
      return calculateTokens({
        width: 512,
        height: 512,
        steps: this.settings.steps,
        batchSize: this.settings.imgCount,
        model: this.settings.model,
        upscale: this.settings.useHiresFix,
        faceRestore: this.settings.faceRestoration,
        denoisingStrength: this.settings.denoisingStrength
      });
    }
  },

  mounted() {
    this.getModelList();
  },

  methods: {
    getModelList() {
      getModel().then(response => {
        const res = response.data;
        // 处理模型列表 (type === 0)
        const type0Items = res.filter(item => item.type === 0);
        const models = type0Items.map(item => ({
          label: item.modelName,
          value: item.modelUuid,
          description: item.modelVersion || ''
        }));

        // 处理模板列表 (type === 2)
        const type2Items = res.filter(item => item.type === 2);
        const templates = type2Items.map(item => {
          if (item.modelName.includes("图生图")) {
            return {
              label: item.modelName,
              value: item.modelUuid,
              description: item.modelVersion || ''
            };
          }
          return null;
        }).filter(item => item !== null);

        // 添加默认选项
        const defaultOption = {
          label: '星流start',
          value: '0',
          description: '默认模型'
        };

        this.modelList = models;
        this.templateList = templates;

        // 添加星流start选项在最前面
        this.templateList.unshift(defaultOption);

        console.log('获取到的模型列表:', this.modelList);
        console.log('获取到的模板列表:', this.templateList);

        // 如果有模型和模板，默认选中第一个
        if (models.length > 0) {
          this.settings.model = models[0].value;
        }

        if (templates.length > 0) {
          this.settings.template = templates[0].value;
        }
      }).catch(error => {
        console.error('获取模型和模板列表失败', error);
        this.$message.error('获取模型和模板列表失败: ' + (error.message || '未知错误'));

        // 添加默认数据以防API失败
        this.modelList = [
          { label: '默认模型', value: 'default_model', description: '预设模型' }
        ];
        this.templateList = [
          { label: '默认模板', value: 'default_template', description: '预设模板' }
        ];

        this.settings.model = 'default_model';
        this.settings.template = 'default_template';
      });
    },

    handleImageUpload(file) {
      console.log('Image uploaded:', file);
      this.sourceImage = file;
    },

    async handleGenerate() {
      // 检查用户是否已经登录
      if (!localStorage.getItem('token')) {
        this.$message.warning('请先登录')
        return
      }

      // 验证表单
      if (!this.sourceImage) {
        this.$message.warning('请上传源图片')
        return
      }

      if (!this.prompts.positive) {
        this.$message.warning('请输入提示词')
        return
      }

      this.isGenerating = true
      this.generationProgress = 0
      this.progressText = '准备生成...'

      try {
        // 准备公共参数
        const prompt = this.prompts.positive
        const imgCount = this.settings.imgCount
        const denoisingStrength = this.settings.denoisingStrength

        // 根据模板类型选择不同的API
        if (this.settings.template === '0') {
          // 星流start模板
          console.log('使用星流start模板生成图片')

          const requestBody = {
            prompt,
            imgCount,
            sourceImage: this.sourceImage,

            width: this.settings.width,
            height: this.settings.height
          }

          console.log('星流start请求参数:', requestBody)

          const response = await getStartImgToImg(requestBody)
          console.log('星流start响应:', response)

          if (response.data && response.data.taskId) {
            this.taskId = response.data.taskId
            this.generateId = response.data.generateId
            this.progressText = '开始生成...'
            await this.pollGenerationResult()
          } else {
            throw new Error('获取任务ID失败')
          }
        } else {
          // 常规图生图模板
          const seed = this.settings.seed === -1 ? null : this.settings.seed
          const params = this.prepareImageToImageParams(prompt, seed, imgCount, denoisingStrength)

          console.log('普通请求参数:', params)

          const response = await Request.post('/api/image/customize/image-to-image', params)

          if (response.data && response.data.taskId) {
            this.taskId = response.data.taskId
            this.generateId = response.data.generateId
            this.progressText = '开始生成...'
            await this.pollGenerationResult()
          } else {
            throw new Error('获取任务ID失败')
          }
        }
      } catch (error) {
        console.error('生成失败:', error)
        this.$message.error('图片生成失败，请稍后再试')
        this.isGenerating = false
        this.progressText = '生成失败'
      }
    },

    // 轮询获取生成结果
    async pollGenerationResult() {
      try {
        const response = await Request.get("/api/image/get-images?&taskId=" + this.taskId);

        if (response) {
          // 检查任务是否完成
          if (response.data.percentCompleted === 1) {
            // 处理结果
            this.generatedImages = response.data.images.map((item, index) => ({
              url: item.imageUrl,
              seed: this.settings.seed === -1 ? `生成的随机种子_${index}` : this.settings.seed
            }));
            this.userTokens -= this.totalTokens;
            this.$message.success('图像生成成功');
            this.isGenerating = false;
            this.progressText = '生成完成';
            return;
          } else {
            // 更新进度
            if (response.data.percentCompleted !== undefined) {
              this.generationProgress = response.data.percentCompleted * 100;
              // 更新进度文本
              const percent = Math.floor(response.data.percentCompleted * 100);
              this.progressText = `生成中... ${percent}%`;
            }
            // 继续轮询
            setTimeout(() => this.pollGenerationResult(), 2000);
          }
        } else {
          throw new Error(response?.message || '获取生成结果失败');
        }
      } catch (error) {
        console.error('获取生成结果失败', error);
        this.$message.error('获取生成结果失败: ' + (error.message || '未知错误'));
        this.isGenerating = false;
        this.progressText = '生成失败';
      }
    },

    async handleStop() {
      try {
        if (this.generateId && this.taskId) {
          // 这里可以添加取消任务的API请求
          await Request.post("/api/image/cancel-task", {
            generateId: this.generateId,
            taskId: this.taskId
          });
        }

        this.isGenerating = false;
        this.generationProgress = 0;
        this.progressText = '已停止生成';
        this.$message.info('已停止生成');
      } catch (error) {
        console.error('停止生成失败', error);
        this.$message.error('停止失败: ' + (error.message || '未知错误'));
      }
    },

    clearResults() {
      this.generatedImages = [];
      this.$message.success('已清除所有结果');
    },

    handleImageClick(image) {
      this.currentPreviewImage = image;
      this.previewDialogVisible = true;
    },

    openBuyTokens() {
      this.creditsDialog.visible = true;
    },

    openVipOptions() {
      this.$message.info('VIP会员功能正在开发中...');
    },

    saveAllImages() {
      if (this.generatedImages.length === 0) {
        this.$message.warning('没有可保存的图像');
        return;
      }

      // 遍历所有图像并下载
      this.generatedImages.forEach((image, index) => {
        this.downloadImage(image, `generated_image_${index + 1}`);
      });

      this.$message.success('所有图像已开始下载');
    },

    downloadImage(image, fileName = 'generated_image') {
      const link = document.createElement('a');
      link.href = image.url;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

    progressFormat(percentage) {
      return `${percentage}%`;
    },

    translatePrompt() {
      if (!this.prompts.positive) {
        this.$message.warning('请先输入提示词');
        return;
      }

      this.$message.info('翻译功能正在开发中...');
    },

    useDefaultNegativePrompt() {
      this.prompts.negative = 'low quality, bad anatomy, worst quality, blurry, distorted face, bad proportions, deformed, ugly, disfigured';
      this.$message.info('已使用默认负向提示词');
    },

    showPromptLibrary() {
      this.$message.info('提示词库功能正在开发中...');
    },

    showNegativeLibrary() {
      this.$message.info('负向提示词库功能正在开发中...');
    },

    refreshModels() {
      this.$message.info('正在刷新模型和模板列表...');
      this.getModelList();
    },

    updateTokenUsage() {
      console.log('更新积分消耗');
    },

    randomizeSeed() {
      this.settings.seed = generateRandomSeed();
      this.$message.info(`已生成随机种子: ${this.settings.seed}`);
    },

    favoriteImage(image) {
      this.$message.success('图片已添加到收藏夹');
    },

    prepareImageToImageParams(prompt, seed, imgCount, denoisingStrength) {
      return {
        templateUuid: this.settings.template,
        checkPointId: this.settings.model,
        prompt,
        negativePrompt: this.prompts.negative || "",
        sampler: this.settings.sampler,
        steps: this.settings.steps,
        cfgScale: this.settings.cfgScale,
        // 添加主尺寸参数
        resizedWidth: this.settings.width || 512,  // 默认512
        resizedHeight: this.settings.height || 512,  // 默认512
        imgCount,
        seed,
        denoisingStrength,
        mode: this.settings.mode,
        clipSkip: this.settings.useClipSkip ? this.clipSkipValue : null,
        restoreFaces: this.settings.faceRestoration ? 1 : 0,
        hiResFixInfoPojo: this.settings.useHiresFix ? {
          hiresSteps: this.settings.hiresSteps,
          hiresDenoisingStrength: this.settings.hiresDenoisingStrength,
          upscaler: this.settings.upscaler,
          resizedWidth: this.settings.resizedWidth || 768,
          resizedHeight: this.settings.resizedHeight || 768
        } : null,
        sourceImage: this.sourceImage,
        userId: 1

      };
    },

    selectCreditPackage(pkg) {
      this.creditsDialog.selectedPackage = pkg;
    },

    confirmCreditPurchase() {
      if (!this.creditsDialog.selectedPackage) return

      const pkg = this.creditsDialog.selectedPackage
      // 在实际应用中，这里应该调用API进行支付处理
      // 模拟购买成功后增加用户积分
      this.userTokens += pkg.amount + (pkg.bonus || 0)
      this.$message.success(`您已成功购买${pkg.amount}积分${pkg.bonus ? '，赠送' + pkg.bonus + '积分' : ''}！`)
      this.creditsDialog.visible = false
      this.creditsDialog.selectedPackage = null
    },

    validateSize() {
      // 确保尺寸是64的倍数
      if (this.settings.width % 64 !== 0 || this.settings.height % 64 !== 0) {
        this.settings.width = Math.round(this.settings.width / 64) * 64;
        this.settings.height = Math.round(this.settings.height / 64) * 64;
        this.$message.warning('尺寸已调整为64的倍数');
      }

      // 确保高清修复尺寸不小于原图尺寸
      if (this.settings.useHiresFix) {
        if (this.settings.resizedWidth < this.settings.width) {
          this.settings.resizedWidth = this.settings.width;
        }
        if (this.settings.resizedHeight < this.settings.height) {
          this.settings.resizedHeight = this.settings.height;
        }
      }
    },
  }
}
</script>

