/**
 * 模拟图像生成过程（用于开发和测试）
 * @param {Object} params 生成参数
 * @param {Function} progressCallback 进度回调函数
 * @returns {Promise<Array>} 生成的图像数组
 */
export function simulateImageGeneration(params, progressCallback) {
  const { batchSize = 1, steps = 30 } = params;
  
  return new Promise((resolve) => {
    const images = [];
    let currentStep = 0;
    
    // 模拟图像生成的步骤进度
    const interval = setInterval(() => {
      currentStep++;
      if (progressCallback) {
        progressCallback({
          step: currentStep,
          totalSteps: steps,
          percentage: Math.floor((currentStep / steps) * 100)
        });
      }
      
      // 生成完成
      if (currentStep >= steps) {
        clearInterval(interval);
        
        // 创建模拟的图像结果
        for (let i = 0; i < batchSize; i++) {
          const randomSeed = generateRandomSeed();
          images.push({
            id: `sim-${Date.now()}-${i}`,
            url: `https://picsum.photos/seed/${randomSeed}/512/512`, // 使用随机图像作为示例
            seed: randomSeed,
            prompt: params.prompt || '模拟生成的图像',
            params: { ...params, seed: randomSeed }
          });
        }
        
        resolve(images);
      }
    }, 100);
  });
}

