import Request from '@/utils/axios'


//获取模型列表
export function getModel(){
    return Request({
        url: '/api/image/model/checkPointId',
        method: 'get'
    })
}

//请求星流start
export function getStartImgToImg(data){
    // 确保参数结构正确
    const requestData = {
        prompt: data.prompt,
        imgCount: data.imgCount,
        sourceImage: data.sourceImage,
        
        width: data.width,
        height: data.height,
        userId: 1
    }
    
    return Request({
        url: '/api/image/start/image-to-image',
        method: 'post',
        data: requestData
    })
}

//请求自定义模型
export function getCustomImgToImg(data){
    return Request({
        url: '/api/image/customize/image-to-image',
        method: 'post',
        data
    })
}

//请求图片接口
export function getImgToImg(query){
    return Request({
        url: '/api/image/get-images',
        method: 'get',
        params: query
    })
}
